import { _decorator, Component, Node, Label, Sprite, tween } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('loading')
export class loading extends Component {

    @property(Sprite)
    loading_spr:Sprite;


    start() {

    }

    onEnable()
    {
        tween(this.loading_spr.node)
        .to(1, { rotation: 360})
        .repeatForever()
        .start()        
    }

    update(deltaTime: number) {
        
    }
}


